import React from 'react';
import {Link} from 'react-router-dom';

const Navbar =()=>(
    <nav>
        <ul>
            <li>
                <a href="/">Home</a>
            </li>
            <li>
                <Link to="/about">About</Link>
            </li>
            <li>
                <Link to="/article-list">Articles</Link>
            </li>

        </ul>
    </nav>
)

export default Navbar