import React,{useState,useEffect} from 'react';
import ArticleList from './ArticleList';
import articleContent from './article-content';
import ArticleComp from '../component/ArticleComponent'
import CommentsList from '../component/Comment';
import CommentForm from '../component/AddCommentForm'
import Upvote from '../component/Upvote';
const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);

    const [articleInfo,setarticleInfo]=useState({upvote:0,comments:[]});
    useEffect(()=>{
        const fetchData=async()=>{
            const result=await fetch(`http://localhost:8000/api/articles/${name}`)
            const body= await result.json();
            console.log(body)
            setarticleInfo(body)
        }

    })

    if (!article) return <h1>Article does not exist!</h1>
    const otherArticle=articleContent.filter(article=> article.name!==name);
    
    return (
        <>
        <h1>{article.title}</h1>
        <Upvote articleName={name} upvote={articleInfo.upvote} setarticleInfo={setarticleInfo}/>
        {article.content.map((paragraph, key) => (
            <p key={key}>{paragraph}</p>
        ))}
        <CommentsList comments={articleInfo.comments}/>
        <CommentForm articleName={name} setArticleInfo={setarticleInfo}/>
        {/* <h3>Other Articles</h3>
            <ArticleComp art={otherArticle}/> */}
        </>
    );
}

export default ArticlePage;